* {
    outline: none;
    
    &:focus {
        outline: none
    }
}
.slick-list {
    .slick-loading & {
        background: #fff url("../img/site/30.gif") center center no-repeat;
    }
}

body {
    font-size: 10px;
    line-height: 19px;
    font-family: 'Gotham', Helvetica, Arial, sans-serif;

    &.no-scroll {
        overflow: hidden;
    }
}

.container {
    @media (max-width: 990px) {
        max-width: 100%;
    }
}

.page {
    &-search {
        border-top: 1px solid #e5eaeb;
    }
    &__map {
        &-wrapper {
            padding: 50px 0 35px;
        }
    }

    &-comparison {
        padding-bottom: 40px;
    }

    &__breadcrumbs {
        margin: 30px 0 15px;

        @media (min-width: 769px) {
            margin: 20px 0;
        }

        @media (min-width: 1201px) {
            margin: 20px 0 40px;
        }
    }
    &__sort {
        font-size: 14px;
        display: flex;
        align-items: center;
    }

    &__listing {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        height: 60px;
        background-color: #e5eaeb;
        border-radius: 15px;
        padding: 10px 5px;

        &-item {
            width: 55px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 15px;
            margin: 0 5px;
            height: 40px;
            cursor: pointer;
            transition: .2s ease-in-out;

            svg {
                &.list {
                    &-row {
                        path {
                            fill: none;
                            stroke: #919fa3;
                            stroke-linecap: round;
                            stroke-width: 1.3px;
                        }
                    }

                    &-grid {
                        height: 15px;

                        circle {
                            fill: #919fa3;
                        }
                    }
                }
            }

            &.active {
                background-color: #fff;
                cursor: default;

                svg {
                    &.list {
                        &-row {
                            path {
                                stroke: #3c4b5a;
                            }
                        }
                        &-grid {
                            circle {
                                fill: #3c4b5a;
                            }
                        }
                    }
                }
            }
        }
    }
}

h1, h2, h3,
.h1, .h2, .h3{
    font-family: 'Trojan';
}

h1,
.h1 {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;

    @media (min-width: 769px) {
        font-size: 24px;
        line-height: 27px;
    }
    @media (min-width: 991px) {
        font-size: 24px;
        line-height: 34x;
    }
    @media (min-width: 1201px) {
        font-size: 36px;
        line-height: 48px;
    }
}

h2,
.h2 {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.67;

    @media (min-width: 992px) {
        font-size: 24px;
    }
}

h3,
.h3 {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.5;
}

h4,
.h4 {
    font-family: 'Gotham', Helvetica, Arial, sans-serif;
    font-size: 18px;
    font-weight: bold;
    line-height: 1.44;
}

p {
    font-size: 16px;
    line-height: 1.5;
}

.block-page {
    padding-top: 52px;
    padding-bottom: 52px;

    &__title {
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        
        .icon {
            margin-right: 15px;
        }

        h2 {
            line-height: 1;
            margin-bottom: 0;
        }

        @media (min-width: 992px) {
            margin-bottom: 37px;
        }
    }
    &__offer {
        .slick-track {
            padding: 15px 0 30px;

            .item {
                margin-left: 15px;
                margin-right: 15px;
            }

            .photo__list {

                    
                .slick-list {
                    border-radius: 15px 15px 0 0;
                }
                .slick-track {
                    padding: 0;
                }
            }
        }
    }
}



.slick-dots {
    li {
        padding: 0;
        margin: 0 5px;

        button {
            padding: 0;

            &::before {
                width: 20px;
                height: 20px;
                font-size: 40px !important;
                padding: 0;
                margin: 0;
                opacity: 1;
                color: $body-color;
            }
        }
    }
}

.mt {
    &-40 {
        margin-top: 20px;
        
        @media (min-width: 1201px) {
            margin-top: 40px;
        }
    }
}

.modal {
    &-backdrop {
        background-color: $body-color;

        &.show {
            opacity: .15;
        }
    }
    &-header {
        align-items: center;
        margin-bottom: 0;
        padding-bottom: 0;
        display: block;
        width: 100%;
        border-bottom: none;
    }
    &-dialog {
        margin: .5rem auto;
    }

    &-xxl {
        width: calc(100% - 20px);
        max-width: 1340px;
    }
    &-xl {
        width: calc(100% - 20px);
        max-width: 950px;

        .modal-body,
        .modal-header {
            @media (min-width: 1201px) {
                padding: 30px 60px;
            }
        }
        .modal-header {
            border-bottom: none;

            h5 {
                font-size: 14px;
                font-weight: 700;
                line-height: 16px;
            }
            button {
                padding-left: 77px;
                padding-right: 77px;

                &.close {
                    padding: 0;
                    padding-right: 1rem;
                }
            }
        }
        .modal-body {
            @media (min-width: 1201px) {
                padding-bottom: 60px;
            }
        }
    }

    &-content {
        box-shadow: 0 15px 30px rgba(#9CA7B2, 0.15);
        border: none;
        border-radius: 15px;
    }

    input {
        &.btn-lg {
            &.btn-warning {
                @media (max-width: 768px) {                    
                    padding: 15px;
                    width: 100%;
                    margin-right: 0 !important;
                }
            }
        }
    }
    .filter__button-clear {
        @media (max-width: 768px) {                    
            margin: 1rem auto 0;
        }
    }
}

.custom-control {
    &-inline {
        margin-top: 20px;
        margin-left: 15px;
    }
    
    label {
        font-size: 16px;
    }
}

.highway-block {
    &__title {
        text-decoration: underline;
    }
}

.play {
    cursor: pointer;

    .triangle {
        fill: #fff;
    }
    .circle-line {
        stroke: #83B173;
        transition: .2s ease-in-out;
    }
    .circle-main {
        transition: .2s ease-in-out;
        fill: #fbb358;
    }
    .circle-main-stroke {
        transition: .2s ease-in-out;
        fill: rgba(#3c4b5a, 0.1);
    }

    &:hover {
        .circle-main {
            fill: #83B173;
        }
    }

    &:focus,
    &:active {
        .circle-main {
            fill: rgba($success, 1);
        }
        .circle-main-stroke {
            fill: rgba($success, 1);
        }
        .circle-line {
            stroke: rgba($success, 1);
        }
    }
}

.tab-switcher {
    background-color: #E5EAEB;
    border-radius: 30px;

    li {
        a {
            display: inline-flex;
            height: 60px;
            font-size: 12px;
            font-weight: 700;
            padding-left: 18px;
            padding-right: 18px;
            border-radius: 30px;
            align-items: center;
            background-color: transparent;
            border: none;
            position: relative;
            z-index: 1;
            box-shadow: none;
            outline: none;
            text-decoration: none;
            color: $body-color;
            min-width: 120px;
            text-align: center;
            justify-content: center;

            @media (min-width: 769px) {
                padding-left: 30px;
                padding-right: 30px;
            }
    
            svg {
                margin-right: 10px;
                margin-top: -2px;
            }
            &[aria-selected="true"] {
                background-color: #3C4B5A;
                color: #fff;
                z-index: 0;
    
                svg {
                    path {
                        fill: #fff;
                    }
                }
            }
        }
    }
    
}

.review {
    margin-top: 10px;

    a {
        font-size: 11px;
        font-weight: 700;
        color: #919FA3;

        @media (min-width: 769px) {
            font-size: 12px;
        }

        svg {
            margin-right: 10px;
            width: 14px;
            path {
                fill: #c7ced0;
            }

            @media (min-width: 991px) {
                width: 18px;
            }
        }
    }
}

.badge {
    &-success {
        color: #78A86D;
        background-color: #EDF8EA;
        font-size: 12px;
        line-height: 14px;
        font-weight: 500;

        svg {
            path {
                fill: #78A86D;
            }
        }
    }
}

.z-index {
    &-0 {
