    }
}

.modal-house {
    max-width: 1295px;
}

.checkbox-house {
    margin-right: 13px;
    margin-bottom: 13px;

    label {
        display: flex;
        align-items: center;
        padding: 0 45px;
        border: 1px solid #E1E8EA;
        cursor: pointer;
        height: 60px;
        line-height: 58px;
        border-radius: 30px;
        font-size: 12px;
        line-height: 16px;
        font-weight: 700;
        position: relative;
        transition: .2s ease-in-out;

        &:before {
            display: block;
            position: absolute;
            top: 17px;
            left: 12px;
            width: 24px;
            height: 24px;
            background: #528048 url(/assets/img/site/tick-white.svg) no-repeat;
            background-position: center center;
            background-size: 10px;
            border: 1px solid #528048;
            border-radius: 50%;
            transition: .2s ease-in-out;
        }
    }

    input {
        &:checked {
            + label {
                background-color: $success;
                border-color: $success;
                color: #fff;
                &:before {
                    content: '';
                }
            }
        }
    }
}
.modal-area-home {
    &__title {
