.work-steps {
    position: relative;
    width: 100%; 
    height: 344; 
    background: url('/assets/img/site/work-bg-desktop@2x.jpg') no-repeat;
    background-size: 100% auto;
    border-radius: 15px;
    flex-direction: column;

    @media (min-width: 991px) {
        height: 448px;
    }
    @media (min-width: 1201px) {
        height: 547px;
    }
    @media (min-width: 1397px) {
        height: 624px;
    }

    .step {
        position: relative;

        &:first-child {
            padding-left: 149px;
            padding-top: 140px;
        }
        
        &-title {
            position: absolute;
            font-size: 14px;
            line-height: 20px;
            cursor: pointer;
            z-index: 9;

            @media (min-width: 1201px) {
                font-size: 16px;
                line-height: 25px;
            }
        }

        &-circle {
            display: block;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            background-color: $success;
            border: 5px solid #fff;
            position: relative;
            cursor: pointer;
            margin: 0 auto;
        }

        &-cloud {
            display: none;
            background-color: rgba($success, 0.89);
            position: absolute;
            left: -124.5px;
            top: 45px;
            width: 224px;
            border-radius: 15px;
            padding: 15px;
            font-size: 14px;
            line-height: 20px;
            color: #fff;
            transition: .2s ease-in-out;
            z-index: 9;

            @media (min-width: 1397px) {
                width: 274px;
            }
            
            &:before {
                content: '';
                display: block;
                border-color: transparent;
                border-style: solid;
                position: absolute;
                top: -12px;
                left: calc(50% - 14px);
                border-width: 0 14px 12px;
                border-bottom-color: rgba($success, 0.89);
            }

            &__footer {
                display: flex;
                justify-content: space-between;
                margin-top: 20px;
            }

            &__prev,
            &__next {
                z-index: 9;
                cursor: pointer;

                svg {
                    path {
                        fill: #fff;
                    }
                }
            }
            &__prev {
                svg {
                    transform: rotate(180deg);
                }
            }
    
            &__num {
                font-size: 16px;
                color: #fff;
    
                span {
                    font-size: 12px;
                    color: #52724C;
                    margin-left: 7px;
                }
            }
        }

        &.active {
            .step-cloud {
                display: block;
            }
        }

        &--left {
            .step-cloud {
                &:before {
                    top: 53px;
                    left: -28px;
                    border-color: transparent;
                    border-right-color: rgba(120,168,109,.89);
                    border-top-width: 12px;
                    border-bottom-width: 12px;
                    border-right-width: 14px;
                }
            }
        }
        &--right {
            .step-cloud {
                &:before {
                    top: 53px;
                    left: 100%;
                    border-color: transparent;
                    border-left-color: rgba(120,168,109,.89);
                    border-top-width: 12px;
                    border-bottom-width: 12px;
                    border-left-width: 14px;
                }
            }
        }
        &--bottom {
            .step-cloud {
                &:before {
                    top: 131px;
                    left: calc(50% - 14px);
                    border-width: 14px 12px 0;
                    border-top-color: rgba($success, 0.89);
                }
            }
        }
        &--top {
            .step-cloud {
                &:before {
                    top: -14px;
                    left: calc(50% - 14px);
                    border-width: 0 12px 14px;
                    border-top-color: rgba($success, 0.89);
                }
            }
        }
            
        &[data-step="1"] {
            .step-title {
                margin-left: -10px;
                margin-top: -30px;

                @media (min-width: 991px) {
                    margin-left: -45px;
                    margin-top: -50px;
                }

                @media (min-width: 1201px) {
                    margin-left: -10px;
                    margin-top: -30px;
                }

                @media (min-width: 1397px) {
                    margin-left: 0;
                    margin-right: 0;
                    margin-top: -0;
                }
            }
            .step-circle {
                margin-left: 18px;
                margin-top: 25px;

                @media (min-width: 991px) {
                    margin-left: -16px;
                    margin-top: -5px;
                }

                @media (min-width: 1201px) {
                    margin-left: 18px;
                    margin-top: 25px;
                }

                @media (min-width: 1397px) {
                    margin-left: 45px;
                    margin-top: 55px;
                }
            }
            .step-cloud {
                left: 45px;
                top: 212px;

                @media (min-width: 991px) {
                    left: 36px;
                    top: 180px;
                }

                @media (min-width: 1201px) {
                    left: 68px;
                    top: 212px;
                }

                @media (min-width: 1397px) {
                    left: 71px;
                    top: 238px;
                }
                
                &__prev {
                    svg {
                        path {
                            fill: #52724C;
                            cursor: default;
                        }
                    }
                }
            }
        }

        &[data-step="2"] {
            .step-title {
