                @media (min-width: 1201px) {
                    margin-left: 375px;
                    margin-top: 10px;
                }

                @media (min-width: 1397px) {
                    margin-left: 445px;
                    margin-top: 10px;
                }
            }
            .step-circle {
                margin-left: 470px;
                margin-top: -30px;

