        }
    }

    &__mobile {
        .btn {
            position: relative;
            padding: 0;
            padding-left: 23px;
            margin-bottom: 15px;
            font-size: 14px;
            font-weight: normal;
            line-height: 18px;
            color: $body-color;
            background-color: transparent;
            
            &:hover,
            &:focus {
                color: $success;
                text-decoration: none;
            }

            &:before {
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                display: block;
                border-radius: 10px;
                background-color: $success;
                left: 0;
                top: 3px;
            }
        }

        p {
            font-size: 12px;
            line-height: 14px;
            margin-bottom: 15px;
            padding-left: 23px;
            color: #919FA3;
        }
    }
}