.selectric-select-bg-white {
    .selectric {
        background-color: #fff;
        border: solid 1px #fff;
    }

    span {
        &.label {
            color: $body-color;
            font-size: 14px;
            font-weight: 700;
            line-height: 1.14;
            padding: 23px 24px;
            margin: 0;
        }
    }

    b {
        &.button {
            background-color: #fff;

            &:after {
                border: none;
                opacity: .7;
                width: 12px;
                height: 7px;
                content: url(/assets/img/site/back.svg);
            }
        }
    }

    &.selectric-hover {
        .selectric {
            border-color: rgba($success, 0.3);
        }
    }
    &.selectric-focus,
    &.selectric-open {
        .selectric {
            border-color: rgba($success, 1);
        }

        .selectric-items {
            border: 1px solid rgba($success, 1);
        }
    }

    &.selectric-open {
        background-color: #fff;

        &.selectric-below {
            .selectric-items {
                background-color: #fff;
                border-top: none;
                border-radius:  0 0 15px 15px;
            }

            .selectric {
                background-color: #fff;
                border-bottom: none;
                border-radius: 15px 15px 0 0;
            }
        }

        &.selectric-above {
            .selectric-items {
                border-bottom: none;
                border-radius: 15px 15px 0 0;
            }

            .selectric {
                border-top: none;
                border-radius:  0 0 15px 15px;
            }
        }
    }
}

