.selectric-wrapper {

    &.selectric-open {
        background-color: #f6f9fb;

        &.selectric-below {
            border-radius: 15px 15px 0 0 !important;

            .selectric-items {
                border-top: none;
                border-radius:  0 0 15px 15px !important;

                li {
                    &:last-child {
                        border-radius:  0 0 14px 14px !important;
                    }

                    &:first-child {
                        border-radius: 0 !important;
                    }
                }
            }

            .selectric {
                border-bottom: none;
                border-radius: 15px 15px 0 0 !important;
            }
        }

        &.selectric-above {
            border-radius: 0 0 15px 15px !important;

            .selectric-items {
                border-bottom: none;
                border-radius: 15px 15px 0 0 !important;

                li {
                    &:first-child {
                        border-radius: 14px 14px 0 0 !important;
                    }

                    &:last-child {
                        border-radius: 0 !important;
                    }
                }
            }

            .selectric {
                border-top: none;
                border-radius: 0 0 15px 15px !important;
            }
        }
    }
}
